import React, { FC } from 'react';

interface Link {
    caption: string;
    url: string;
}

const links: Link[] = [
    {
        caption: ' A',
        url: '#group-a',
    },
    {
        caption: ' B',
        url: '#group-b',
    },
];

const Navigation: FC = () => {
    return (
        <nav className="navigation">
            <ul>
                {links.map(({ caption, url }) => {
                    return (
                        <li key={url}>
                            <a href={url}>{caption}</a>
                        </li>
                    );
                })}
            </ul>
        </nav>
    );
};

export default Navigation;
